import React from "react";

type ImgProps = JSX.IntrinsicElements["img"];

interface PlaceholderImageProps extends ImgProps {
  width?: number;
  height?: number;
}

export const PlaceholderImage: React.FC<PlaceholderImageProps> = (props) => {
  let { width = 200, height, alt = "image", ...imgProps } = props;
  height = height || width;

  const placeholderText = `${alt}(${width.toString()}x${height.toString()})`;

  return (
    <img
      src={`https://via.placeholder.com/${width}x${height}?text=${placeholderText}`}
      alt="placeholder"
      {...imgProps}
    />
  );
};
