import React, { useMemo } from 'react';
import { formatUnits } from 'ethers/lib/utils';
import numeral from 'numeral';
import { BigNumber } from 'ethers';
import { TokenValue } from '../../sdk/Base';

export type TokenAmountProps = { value: TokenValue | BigNumber; ticker?: string };
export const TokenAmount: React.FC<TokenAmountProps> = ({ value, ticker }) => {
  const amount = value instanceof TokenValue ? value.value : value;
  const symbol = value instanceof TokenValue ? value.currency.ticker : ticker;

  const displayNumber = useMemo(() => numeral(formatUnits(amount)).format('0,0.[0000]'), [amount]);

  return (
    <>
      {displayNumber} {symbol}
    </>
  );
};
