import { useMemo } from 'react';
import { ethers } from 'ethers';
import { NftMinter } from 'sdk/MintNftTypes';
import { MintNftSdk } from 'sdk/MintNftSdk';

export interface DegenSdk {
  bigshotMinter: NftMinter;
  gangsterMinter: NftMinter;
  bigshotWhitelistMinter: NftMinter;
  gangsterWhitelistMinter: NftMinter;
}

export const useDegenSdk = () => {
  const provider = useMemo(() => new ethers.providers.Web3Provider(window.ethereum), []);

  const bigshotMinter = useMemo(() => new MintNftSdk('Bigshot', 'SALE', provider), [provider]);
  const bigshotWhitelistMinter = useMemo(() => new MintNftSdk('Bigshot', 'PRE_SALE', provider), [provider]);
  const gangsterMinter = useMemo(() => new MintNftSdk('Gangster', 'SALE', provider), [provider]);
  const gangsterWhitelistMinter = useMemo(() => new MintNftSdk('Gangster', 'PRE_SALE', provider), [provider]);

  return { bigshotMinter, gangsterMinter, bigshotWhitelistMinter, gangsterWhitelistMinter };
};
