import React, { useMemo } from 'react';
import { NftMinter } from 'sdk/MintNftTypes';
import { shortenIfAddress, useEtherBalance, useEthers } from '@usedapp/core';
import { FaSpinner } from 'react-icons/fa';
import { TokenAmount } from 'components';
import { useQuery } from 'react-query';

export const AccountMintInfo: React.FC<{ minter: NftMinter }> = ({ minter }) => {
  const { price } = minter.getConfig();
  const { account } = useEthers();
  const etherBalance = useEtherBalance(account);

  const balance = useMemo(() => {
    if (!account) return '-';
    if (!etherBalance) return <FaSpinner className="animate-spin" />;
    return <TokenAmount value={etherBalance} ticker="ether" />;
  }, [account, etherBalance]);

  const { data: mintedCount, isLoading } = useQuery(
    ['fetchMintedRecordsByAddress', { account }],
    () => minter.fetchMintedRecordsByAddress(account!).then((records) => records.length),
    { enabled: !!account }
  );

  return (
    <div>
      <div className="max-w-lg mx-auto flex justify-between text-white text-2xl">
        <div>Price</div>
        <TokenAmount value={price} />
      </div>

      <div className="text-white mt-4 text-lg">{shortenIfAddress(account)}</div>

      {account && (
        <>
          <div className="max-w-lg mx-auto flex justify-between text-white text-2xl">
            <div>Balance</div>
            <div>{balance}</div>
          </div>

          <div className="max-w-lg mx-auto flex justify-between text-white text-2xl">
            <div>Minted(mine)</div>
            <div>{isLoading ? <FaSpinner className="animate-spin" /> : mintedCount}</div>
          </div>
        </>
      )}
    </div>
  );
};
