import React, { useMemo } from 'react';
import { useEtherBalance, useEthers } from '@usedapp/core';
import { Faq } from './Faq';
import { NftMinter } from '../../sdk/MintNftTypes';
import dayjs from 'dayjs';
import { useMutation, useQuery } from 'react-query';
import { toast } from 'react-toastify';
import { useDegenConfig } from 'hooks/useDegenConfig';
import { NftDescription } from './Description';
import { ConnectWalletButton, Countdown, PixieButton, PlaceholderImage, Title } from 'components';
import { AccountMintInfo } from './AccountMintInfo';

function MintButton({ minter }: { minter: NftMinter }) {
  const { startTime, endTime, price } = minter.getConfig();
  const betweenStartAndEnd = dayjs().isAfter(startTime) && dayjs().isBefore(endTime);

  const { account, chainId: walletChainId, switchNetwork } = useEthers();
  const { chainId: expectChainId } = useDegenConfig();
  const shouldSwitchNetwork = expectChainId !== walletChainId;

  const etherBalance = useEtherBalance(account);
  const { mutate: mint, isLoading: isMinting } = useMutation(() => minter.mint().then((tx) => tx.wait()), {
    onSuccess: () => void toast.success('Minted successfully'),
    onError: (error) => {
      console.log(error);
      void toast.error('Mint failed');
    },
  });

  const { data: whitelistStatus, isLoading: isLoadingInWhitelist } = useQuery(
    ['queryWhitelistStatus', { account }],
    () => Promise.all([minter.checkIsInWhitelist(account!), minter.hasMintedViaWhitelist(account!)]),
    { enabled: !!account }
  );

  const [isInWhitelist, hasMintedViaWhitelist] = whitelistStatus || [false, false];

  const isLoading = isMinting || isLoadingInWhitelist;

  const notStarted = dayjs().isBefore(startTime);
  const ended = dayjs().isAfter(endTime);
  const insufficientFunds = etherBalance?.lt(price.value) ?? false;

  const disabled = useMemo(
    () => isLoading || !betweenStartAndEnd || !etherBalance || insufficientFunds || !isInWhitelist,
    [isLoading, betweenStartAndEnd, etherBalance, insufficientFunds, isInWhitelist]
  );

  const buttonText = useMemo(() => {
    if (isInWhitelist === false) return 'Not in whitelist';
    if (hasMintedViaWhitelist) return 'You have minted via whitelist';
    if (notStarted) return 'Minting not started yet';
    if (ended) return 'Minting ended';
    if (!etherBalance) return 'Mint';
    if (insufficientFunds) return 'Insufficient funds';

    return 'Mint';
  }, [ended, etherBalance, hasMintedViaWhitelist, insufficientFunds, isInWhitelist, notStarted]);

  return (
    <PixieButton
      loading={!etherBalance || isLoading}
      block
      disabled={disabled}
      onClick={() => (shouldSwitchNetwork ? switchNetwork(expectChainId) : mint())}
      type="primary"
    >
      {buttonText}
    </PixieButton>
  );
}

export const WhitelistMinter: React.FC<{ minter: NftMinter; title: React.ReactNode }> = ({ minter, title }) => {
  const { account } = useEthers();

  return (
    <div className="px-4 container mx-auto">
      <div className="max-w-2xl mx-auto text-center">
        <Title level={2}>{title}</Title>
        <Title level={3}>(Whitelist)</Title>

        <div className="max-w-lg mx-auto">
          <Countdown targetDatetime={minter.getConfig().endTime}></Countdown>
        </div>

        <div className="flex justify-center my-8">
          <PlaceholderImage width={400} />
        </div>

        <AccountMintInfo minter={minter} />

        <div className="max-w-lg mt-4 mb-10 mx-auto">
          {account ? <MintButton minter={minter} /> : <ConnectWalletButton type="primary" />}
        </div>
      </div>

      <Faq />
      <NftDescription />
    </div>
  );
};
