import { CurrencyValue, Token } from '@usedapp/core';
import { BigNumber, BigNumberish } from 'ethers';

export class TokenValue extends CurrencyValue {
  readonly currency: Token;

  constructor(token: Token, value: BigNumberish) {
    super(token, BigNumber.from(value));
    this.currency = token;
  }
}

export const Ether = new Token('Ethereum', 'ether', 1, '0x0000000000000000000000000000000000000000', 18);
