import { MintConfig, MintRecord, NftMinter } from './MintNftTypes';
import { BytesLike, ContractTransaction } from 'ethers';
import { mockContractTransaction } from '../mock';
import { Ether, TokenValue } from './Base';
import { parseEther } from 'ethers/lib/utils';
import { JsonRpcProvider } from '@ethersproject/providers';
import dayjs from 'dayjs';
import { Bigshot, Bigshot__factory, DegenSale, DegenSale__factory, Gangster, Gangster__factory } from './contracts';
import bigshotMerkleData from './merkleData/bigshotMerkleData.json';
import gangsterMerkleData from './merkleData/gangsterMerkleData.json';

type NFTKind = 'Bigshot' | 'Gangster';

export class MintNftSdk implements NftMinter {
  kind: NFTKind;
  private provider: JsonRpcProvider;
  private saleContract: DegenSale;
  private bigshotContract: Bigshot;
  private gangsterContract: Gangster;
  private config: MintConfig;

  constructor(kind: 'Bigshot' | 'Gangster', saleType: 'PRE_SALE' | 'SALE', provider: JsonRpcProvider) {
    this.kind = kind;
    this.provider = provider;

    this.saleContract = DegenSale__factory.connect(process.env.REACT_APP_SALE_CONTRACT, provider);
    this.bigshotContract = Bigshot__factory.connect(process.env.REACT_APP_BIGSHOT_CONTRACT, provider);
    this.gangsterContract = Gangster__factory.connect(process.env.REACT_APP_GANGSTER_CONTRACT, provider);
    this.config = {
      saleType,
      contractAddress: process.env.REACT_APP_SALE_CONTRACT,
      startTime: dayjs(process.env.REACT_APP_SALE_START_TIME).valueOf(),
      endTime: dayjs(process.env.REACT_APP_SALE_END_TIME).valueOf(),
      price: new TokenValue(
        Ether,
        parseEther(this.kind === 'Bigshot' ? process.env.REACT_APP_BIGSHOT_PRICE : process.env.REACT_APP_GANGSTER_PRICE)
      ),
    };
  }

  async checkIsInWhitelist(address?: string): Promise<boolean> {
    if (this.kind === 'Bigshot') return bigshotMerkleData.proofs.some((value) => value.address === address);
    if (this.kind === 'Gangster') return gangsterMerkleData.proofs.some((value) => value.address === address);
    return Promise.resolve(false);
  }

  getConfig(): MintConfig {
    return this.config;
  }

  async fetchMintedRecordsByAddress(address?: string): Promise<MintRecord[]> {
    if (!address) return Promise.resolve([]);
    const nftContract = this.kind === 'Bigshot' ? this.bigshotContract : this.gangsterContract;
    try {
      const tokensOfOwner = await nftContract.tokensOfOwner(address);
      return tokensOfOwner.map((value) => {
        return {
          index: value.toNumber(),
        };
      });
    } catch (e) {
      // If tokensOfOwner was none, the contract would revert.
      return Promise.resolve([]);
    }
  }

  // FIXME not decided to impl this feature or not
  fetchMintedStatus(): Promise<{ issuedCount: number; totalCount: number }> {
    return Promise.resolve({ issuedCount: 0, totalCount: 0 });
  }

  // TODO DegenSale contract not supported to this, also the method not used in frontend for now
  hasMintedViaWhitelist(address?: string): Promise<boolean> {
    return Promise.resolve(false);
  }

  async mint(count?: number): Promise<ContractTransaction> {
    const signer = this.provider.getSigner();
    if (this.kind === 'Bigshot') {
      if (this.config.saleType === 'PRE_SALE') {
        const merkleProof = this.getMerkleProof(await signer.getAddress());
        console.log('signer', await signer.getAddress());
        console.log('proof ', merkleProof);
        if (!merkleProof) throw new Error('user not in whitelist could not mint presale');
        return this.saleContract.connect(signer).bigshotPresale(merkleProof, {
          value: this.config.price.value,
        });
      } else {
        return this.saleContract.connect(signer).bigshotPurchase({
          value: this.config.price.value,
        });
      }
    } else {
      if (this.config.saleType === 'PRE_SALE') {
        const merkleProof = this.getMerkleProof(await signer.getAddress());
        if (!merkleProof) throw new Error('user not in whitelist could not mint presale');
        return this.saleContract.connect(signer).gangsterPresale(merkleProof, {
          value: this.config.price.value,
        });
      } else {
        return this.saleContract.connect(signer).gangsterPurchase({
          value: this.config.price.value,
        });
      }
    }
  }

  getMerkleProof(address: string): string[] | undefined {
    if (this.kind === 'Bigshot') {
      return bigshotMerkleData.proofs.find((value) => value.address === address)?.proof;
    } else {
      return gangsterMerkleData.proofs.find((value) => value.address === address)?.proof;
    }
  }

  // not used, maybe should be depreciated
  async mintViaWhitelist(proof: BytesLike[]): Promise<ContractTransaction> {
    return mockContractTransaction();
  }
}
