import React from 'react';
import { Minter } from './Minter';
import { useDegenSdk } from 'hooks/useDegenSdk';
import { Route, Routes, useNavigate } from 'react-router-dom';
import { PixieButton } from 'components/Button';
import { WhitelistMinter } from './WhitelistMinter';
import { Title } from 'components';

const MinterSelector: React.FC = () => {
  const navigate = useNavigate();
  return (
    <div className="flex flex-col gap-8 justify-center items-center py-20">
      <PixieButton type="primary" onClick={() => navigate('/mint/bigshot')}>
        Bigshot
      </PixieButton>
      <PixieButton type="primary" onClick={() => navigate('/mint/bigshot/whitelist')}>
        Bigshot(whitelist)
      </PixieButton>
      <PixieButton type="primary" onClick={() => navigate('/mint/gangster')}>
        Gangster
      </PixieButton>
      <PixieButton type="primary" onClick={() => navigate('/mint/gangster/whitelist')}>
        Gangster(whitelist)
      </PixieButton>
    </div>
  );
};

const MinterEntry: React.FC = () => {
  const { bigshotMinter, gangsterMinter, bigshotWhitelistMinter, gangsterWhitelistMinter } = useDegenSdk();

  return (
    <Routes>
      <Route index element={<MinterSelector />} />
      <Route
        path="/bigshot"
        element={<Minter minter={bigshotMinter} title={<Title level={2}>Degen Bigshot Sale</Title>} />}
      />
      <Route
        path="/bigshot/whitelist"
        element={
          <WhitelistMinter minter={bigshotWhitelistMinter} title={<Title level={2}>Degen Bigshot Sale</Title>} />
        }
      />
      <Route
        path="/gangster"
        element={<Minter minter={gangsterMinter} title={<Title level={2}>Degen Gangster Sale</Title>} />}
      />
      <Route
        path="/gangster/whitelist"
        element={
          <WhitelistMinter minter={gangsterWhitelistMinter} title={<Title level={2}>Degen Gangster Sale</Title>} />
        }
      />
    </Routes>
  );
};

export const MintView: React.FC = () => {
  if (!window.ethereum) {
    return (
      <div className="pt-16">
        <Title>Please install MetaMask first</Title>
      </div>
    );
  }
  return <MinterEntry />;
};

export default MintView;
