import { BigNumber, BigNumberish } from 'ethers';

interface Provider {
  request(payload: { method: string; params: any[] }): Promise<void>;
}

export async function switchNetwork(
  payload: { chainId: BigNumberish; rpcUrl?: string },
  provider?: Provider
): Promise<void> {
  // chainId must be in hexadecimal numbers
  const chainId = '0x' + BigNumber.from(payload.chainId).toNumber().toString(16);
  provider = provider || window.ethereum;

  if (!provider) throw new Error('No ethereum provider found');

  return provider
    .request({
      method: 'wallet_switchEthereumChain',
      params: [{ chainId }],
    })
    .catch(() => {
      return provider!.request({
        method: 'wallet_addEthereumChain',
        params: [{ chainId, rpcUrl: payload.rpcUrl }],
      });
    });
}
