import { BigNumber, ContractReceipt, ContractTransaction } from 'ethers';
import { asyncSleep } from './utils';
import { hexlify, randomBytes } from 'ethers/lib/utils';

export function mockContractTransaction(): ContractTransaction {
  return {
    async wait(): Promise<ContractReceipt> {
      await asyncSleep(500);
      return {
        logs: [],
        to: hexlify(randomBytes(20)),
        from: hexlify(randomBytes(20)),
        contractAddress: hexlify(randomBytes(20)),
        transactionIndex: 0,
        gasUsed: BigNumber.from(0),
        logsBloom: '0x',
        blockHash: '0x',
        transactionHash: '0x',
        blockNumber: 0,
        confirmations: 0,
        cumulativeGasUsed: BigNumber.from(0),
        effectiveGasPrice: BigNumber.from(0),
        byzantium: false,
        type: 0,
      };
    },
    hash: hexlify(randomBytes(32)),
    confirmations: 0,
    from: '',
    gasLimit: BigNumber.from(0),
    data: '',
    chainId: 0,
    nonce: 0,
    value: BigNumber.from(0),
  };
}
