import { shortenIfAddress, useEthers } from '@usedapp/core';
import React, { useCallback, useEffect } from 'react';
import { PixieButton, PixieButtonProps } from '../Button';
import { useDegenConfig } from '../../hooks/useDegenConfig';
import { switchNetwork } from 'utils/switchNetwork';

export const ConnectWalletButton: React.FC<PixieButtonProps> = (props) => {
  const { chainId: expectedChainId } = useDegenConfig();
  const { account, activateBrowserWallet, chainId: walletChainId, isLoading } = useEthers();

  useEffect(() => {
    if (walletChainId === undefined) return;
    if (expectedChainId !== walletChainId) void switchNetwork({ chainId: expectedChainId });
  }, [walletChainId, expectedChainId]);

  const connectToWallet = useCallback(() => {
    if (account) return;
    activateBrowserWallet();
  }, [account, activateBrowserWallet]);

  if (account) {
    return <PixieButton {...props}>{shortenIfAddress(account)}</PixieButton>;
  }

  return (
    <PixieButton loading={isLoading} onClick={connectToWallet} {...props}>
      Connect
    </PixieButton>
  );
};
