import React, { useEffect, useState } from 'react';
import dayjs from 'dayjs';
import { PixieWrapper } from '../Button';

export type TargetDatetime = string | number | Date;

export interface CountdownProps {
  targetDatetime: TargetDatetime;
  interval?: number;
}

function useCountdown(config: CountdownProps) {
  const { targetDatetime, interval = 1000 } = config;
  const [remaining, setRemaining] = useState({ days: 0, hours: 0, minutes: 0, seconds: 0 });

  useEffect(() => {
    function refreshRemaining() {
      const now = dayjs();
      const target = dayjs(targetDatetime);

      if (target.isBefore(now)) {
        clearInterval(intervalId);
        return;
      }

      const days = target.diff(now, 'day');
      const hours = target.subtract(days, 'day').diff(now, 'hour');
      const minutes = target.subtract(days, 'day').subtract(hours, 'hour').diff(now, 'minute');
      const seconds = target
        .subtract(days, 'day')
        .subtract(hours, 'hour')
        .subtract(minutes, 'minute')
        .diff(now, 'second');

      setRemaining({ days, hours, minutes, seconds });
    }

    refreshRemaining();
    const intervalId = setInterval(refreshRemaining, interval);

    return () => clearInterval(intervalId);
  }, [interval, targetDatetime]);

  return remaining;
}

export const Countdown: React.FC<CountdownProps> = (props) => {
  const { days, hours, minutes, seconds } = useCountdown(props);

  return (
    <div className="flex justify-between gap-4 md:gap-8">
      <PixieWrapper borderColor="#3564E3" className="w-16 h-16 md:w-20 md:h-20 text-2xl">
        {days}
        <br />
        <span className="text-sm">days</span>
      </PixieWrapper>
      <PixieWrapper borderColor="#3564E3" className="w-16 h-16 md:w-20 md:h-20 text-2xl">
        {hours}
        <br />
        <span className="text-sm">hours</span>
      </PixieWrapper>
      <PixieWrapper borderColor="#3564E3" className="w-16 h-16 md:w-20 md:h-20 text-2xl">
        {minutes}
        <br />
        <span className="text-sm">minutes</span>
      </PixieWrapper>
      <PixieWrapper borderColor="#3564E3" className="w-16 h-16 md:w-20 md:h-20 text-2xl">
        {seconds}
        <br />
        <span className="text-sm">seconds</span>
      </PixieWrapper>
    </div>
  );
};
